html, body {
  margin: 0;
  background: transparent;
  min-height: 0;
}

@media only screen and (max-width: 2500px) {
  .container .CodeMirror__container .CodeMirror {
    font-size: 14px;
  }
}

@media only screen and (max-width: 600px) {
  .container .CodeMirror__container .CodeMirror {
    font-size: 13px;
  }
}

@media only screen and (max-width: 500px) {
  .container .CodeMirror__container .CodeMirror {
    font-size: 13px;
  }
}

@media only screen and (max-width: 400px) {
  .container .CodeMirror__container .CodeMirror {
    font-size: 12px;
  }
}

.videoWrapper {
	position: relative;
	padding-bottom: 56.25%; /* 16:9 */
	padding-top: 25px;
	height: 0;
	overflow: hidden;
  margin-bottom: 20px;
}
.videoWrapper iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 90%;
	height: 90%;
}

@media only screen and (min-width: 600px) {
	.paddedText {
		margin-left: 16px;
	}
}

@media only screen and (max-width: 600px) {
	.paddedText {
		margin-left: 0px;
	}
}

